.td-dialog-title {
  margin-top: 0;
  margin-bottom: 20px; }

.td-dialog-content {
  margin-bottom: 16px; }

.td-dialog-actions {
  position: relative;
  top: 16px;
  left: 16px; }
  ::ng-deep [dir='rtl'] .td-dialog-actions {
    right: 16px;
    left: auto; }

:host {
  display: block; }
  :host .td-dialog-actions {
    flex-direction: row;
    box-sizing: border-box;
    display: flex; }
    :host .td-dialog-actions .td-dialog-spacer {
      flex: 1; }
    :host .td-dialog-actions ::ng-deep button {
      text-transform: uppercase;
      margin-left: 8px;
      padding-left: 8px;
      padding-right: 8px;
      min-width: 64px; }
      [dir='rtl'] :host .td-dialog-actions ::ng-deep button {
        margin-right: 8px;
        margin-left: inherit; }
